import React from 'react'

interface ListProps {
  dataSource: Array<any>
  renderItem: (item: any, key?: any) => React.ReactNode
}

const List: React.FC<ListProps> = (props) => {
  const {dataSource, renderItem} = props
  return (
    <ul>
      {dataSource.map(renderItem)}
    </ul>
  )
}

List.propTypes = {

}

export default List