
import fetchJsonp from "fetch-jsonp"
export const apikey = '0df993c66c0c636e29ecbb5344252a4a'
const baseApiUrl = 'https://api.douban.com/v2/book'

const searchApi = (value: string) => {
  return `${baseApiUrl}/search?apikey=${apikey}&q=${value}&fields=title,isbn13&count=10`
}
const matchSearchApi = (value: string) => {
  return `${baseApiUrl}/search?apikey=${apikey}&q=${value}&fields=title,subtitle,author,rating,pubdate,images,publisher,isbn13,alt&count=10`
}
const isbnApi = (isbn: string) => {
  return `${baseApiUrl}/isbn/${isbn}?apikey=${apikey}`
}
const newBookApi = `${baseApiUrl}/search?apikey=${apikey}&tag=2019`

export const searchFetch = async (value: string) => {
  return await fetchJsonp(searchApi(value)).then((res) => res.json())
}
export const matchSearchFetch = async (value: string) => {
  return await fetchJsonp(matchSearchApi(value)).then((res) => res.json())
}
export const isbnFetch = async (isbn: string) => {
  return await fetchJsonp(isbnApi(isbn)).then((res) => res.json())
}
export const newBookFetch = async () => {
  return await fetchJsonp(newBookApi).then((res) => res.json())
}
