import {shallowEqual, useDispatch} from "react-redux"
import {useEffect } from "react"
import {fetchNewBook} from "./redux/actions"
import {useSelector} from "../../common/reduxHook"

const getNewBook = (state: any) => state.newBook

export const useNewBook = () => {
  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(fetchNewBook())
  }, [])
  const fetchNewBookData = () => dispatch(fetchNewBook())

  const data = useSelector(state => state.newBook, shallowEqual)
  return {data,fetchNewBookData}
}
