import {newBookFetch} from "../../../api/service"
import {CommonActions} from "../../../common"

export const fetchNewBook = () => {
  return (dispatch: any, getState: any) => {
    dispatch(CommonActions.fetchStarted())
    newBookFetch()
      .then(data => {
        dispatch(CommonActions.fetchSuccess(data))
      })
      .catch(err => {
        dispatch(CommonActions.fetchFailure(err))
      })
  }
}