import {CommonActionTypes, CommonStatus} from "../../../common"


export default (state = {status: CommonStatus.LOADING}, action:any) => {
  switch (action.type) {
    case CommonActionTypes.FETCH_FAILURE: {
      return {status: CommonStatus.LOADING}
    }
    case CommonActionTypes.FETCH_SUCCESS: {
      return {...state, status: CommonStatus.SUCCESS, ...action.result}
    }
    case CommonActionTypes.FETCH_FAILURE: {
      return {status: CommonStatus.FAILURE}
    }
    default: {
      return state
    }
  }
}