import React, {useEffect, useRef, useState} from 'react'
import { Switch, Route } from 'react-router';
import { SearchBar,SearchHistory, SearchList } from "../components"
import DetailsList from "../../details_list/DetailsList"
import {useNowSearch} from "../hook"


const Search: React.FC = (props:any) => {
  const {nowSearch} = useNowSearch()
  return (
    <div>
      <SearchBar />
      <Switch>
        <Route exact path='/search/' component={SearchHistory} />
        <Route exact path='/search/q' component={SearchList} />
        <Route exact path='/search/match' render={()=>(
          <DetailsList dataSource={nowSearch} />
        )} />
      </Switch>
    </div>
  )
}

export default Search