import ActionTypes from "./actionTypes"
import {CommonActionTypes,CommonStatus} from "../../../common"

const initialState = {
  value: '',
  historySearch: new Set<string>(),
  nowSearch: null,
  specificSearch: null
}

const searchReducer = (state = initialState, action: any) => {
  switch (action.type) {
    //更改
    case ActionTypes.CHANGE_VALUE: {
      return {...state, value: action.value}
    }
    case ActionTypes.CHANGE_HISTORY_SEARCH: {
      console.log(action.historySearch)
      return {...state, historySearch: action.historySearch}
    }
    case ActionTypes.CHANGE_NOW_SEARCH: {
      return {...state, nowSearch: action.nowSearch}
    }
    case ActionTypes.CHANGE_SPECIFIC_SEARCH: {
      return {...state, historySearch: action.specificSearch}
    }
    case CommonActionTypes.FETCH_STARTED: {
      return {...state, status: CommonStatus.LOADING}
    }
    case CommonActionTypes.FETCH_SUCCESS: {
      return {...state, status: CommonStatus.SUCCESS, nowSearch: action.result}
    }
    case CommonActionTypes.FETCH_FAILURE: {
      return {...state, status: CommonStatus.FAILURE}
    }
    default:
      return state
  }
}

export default searchReducer
