import React from 'react'
import { FiMenu, FiSearch } from 'react-icons/fi';
import {IconWrap} from "../index"
import './app_bar.scss'
import {Link} from "react-router-dom"
interface AppBarProps {

}

const AppBar: React.FC<AppBarProps> = (props) => {
  const classPrefix = 'app-bar'
  return (
    <div className={classPrefix} >
      <IconWrap icon={<FiMenu/>} style={{marginLeft: '-12px'}}/>
      <div className={`${classPrefix}__title`}>
        <h1></h1>
      </div>
      <Link to="/search" className={`${classPrefix}__right`}>
        <IconWrap icon={<FiSearch/>} style={{marginRight: '-12px'}} />
      </Link>
    </div>
  )
}

export default AppBar
